.wrapper {
  max-width: 800px;
  margin: 100px auto;
}

.grid {
  display: flex;
  align-items: baseline;
  padding: 40px 0;
  border-top: 1px solid #eee;
}

.grid:last-child {
  border-bottom: 1px solid #eee;
}

.grid > div {
  flex: 1;
}

.grid > div:nth-child(2) {
  margin: 0 40px;
}

.grid > div:nth-child(3) {
  text-align: right;
}

pre {
  display: inline-flex;
  padding: 4px 8px 3px;
  font-family: Menlo, monospace;
  font-size: 14px;
  color: #fff;
  border-radius: 4px;
  background: #394046;
}
